/* Оформление нового горизонтального меню */
#mainmenu {
height: 40px;
position: relative;
padding: 0;
z-index: 10; }
#mainmenu ul, #mainmenu ul ul, #mainmenu ul ul li {
list-style: none;
padding: 0;
margin: 0; }
#mainmenu ul {
padding-top: 10px;
text-align: center;
text-transform: uppercase; }
#mainmenu ul li {
padding: 0;
margin: 0;
position: relative;
display: inline-block;
}
#mainmenu ul li a {
color: #fff;
font-family: "PoppinsRegular", sans-serif;
padding: 30px 20px;
@include font-size(15px);
text-decoration: none;
@include trans;
position: relative;}
#mainmenu ul li a:hover {
padding: 30px 20px;}
#mainmenu > ul > li.parent > a{
padding: 30px 20px;
}
#mainmenu ul li a:hover:before {
top:20px;
left:50%;
margin-left: -10px;
position: absolute;
content: '';
 width: 20px;
 height: 2px;
 background-color: #fff;
}
/** Это пример работы треугольника возле ссылким с выпадашкой  **/
#mainmenu > ul > li.parent:after {
content: "\f103";
color: #fff;
font-family: "FontAwesome";
display: inline-block;
vertical-align: middle;
margin: -2px 0 0 0;
font-size: 1.0rem; }
/*************************** Âûïàäàþùèå ïîäïóíêòû **/
#mainmenu ul li ul {
background: #302E4E;
border-radius: 3px;
@include trans;
text-transform: none;
opacity: 0;
visibility: hidden;
position: absolute;
left: 0;
top: 40px;
font-size: 12px;
margin-top: 20px;
height: auto;
min-width: 300px;
width: 100%; }
#mainmenu ul li:hover > ul {
border-top: 5px solid $color_blues;
   border-bottom: 5px solid #302E4E;
opacity: 1;
visibility: visible;
transition: margin 0.3s ease 0s;
margin: 0;
z-index: 11; }
#mainmenu > ul > li > ul > li.parent > a::after {
content: "\f103";
font-family: "FontAwesome";
display: inline-block;
vertical-align: middle;
margin: 0px 0 0 8px;
font-size: 1.0rem; }
#mainmenu ul li ul li {
display: block;
position: relative;
left: 0;
height: auto;
text-align: left !important; }
#mainmenu ul li ul li a {
display: block;
padding: 10px 15px 10px 25px;
border: none;
font-size: .95rem;
text-transform: uppercase;
position: relative; }
#mainmenu ul li ul li a:hover:before {
display: none;
}
/*************************** Âûïàäàþùèå ïóíêòû âòîðîãî óðîâíÿ **/
#mainmenu ul li ul li ul {
border-top: 5px solid $color_blues;
top: 0;
left: 100%;
margin: 0 0 0 20px;
}
#mainmenu ul ul li:first-child > a {
border-radius: 3px 3px 0 0; }
#mainmenu > ul > li.parent:hover > a::before {
visibility: visible;
@include trans; }
.mainbuttons {
