/* Video Popup Css */

.video-play-button {
    display: inline-block;
    width: 32px;
    height: 44px;
    border-radius: 50%;
    padding: 18px 20px 18px 28px;
    position: relative;
    margin: 40px 0 55px 0;
}

.video-play-button:focus,
.video-play-button:active {
    outline: none;
    box-shadow: none;
}

.video-play-button:before {
    content: "";
    position: absolute;
    z-index: 0;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    display: block;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: #253cac;
    animation: pulse-border 1500ms ease-out infinite;
}

.video-play-button:after {
    content: "";
    position: absolute;
    z-index: 1;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    display: block;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    transition: all 200ms;
    background:lighten(#253cac, 10%);

}

.video-play-button:hover:after {
    background:darken(#253cac, 10%);
}

.video-play-button i {
    color: #fff;
    z-index: 2;
    position: absolute;
    font-size: 45px;
    left: 10px;
    top: 0;
}

.video-popup-content p {
    color: #ffffff;
    font-size: 19px;
    line-height: 1.5;
}

.video-popup-content h1 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #ffffff;
}

@keyframes pulse-border {
    0% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
        opacity: 1;
    }
    100% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
        opacity: 0;
    }
}

@-webkit-keyframes pulse-border {
    0% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
        opacity: 1;
    }
    100% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
        opacity: 0;
    }
}

